.App {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(8px + 1vmin);
  color: white;
}

.left-bar {
  position: fixed;
  top: 80px;
  width: 200px;
  padding: 40px;
  height: 100%;
}

.top-container {
  position: fixed;
  height: 80px;
  width: calc(100% - 400px);
  left: 200px;
  text-align: center;
  z-index: 99;
}

.game-container {
  position: fixed;
  width: calc(100% - 320px);
  padding: 100px 160px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.best {
  color: cyan;
}

.good {
  color: green;
}

.dud {
  color: red;
}

* {
  cursor: none;
}

.cursor {
  position: fixed;
  top: -1000px;
  left: -1000px;
  transform: scale(60%) translate(-80%, -80%);
  will-change: width, height, transform, border;
  z-index: 999;
  pointer-events: none;
}

.inventory {
  top: 16;
  left: 16;
  float: left;
}

.gameboard {
  background-color: #181c14;
  width: 800px;
  height: 600px;
}

.grid-wrapper {
  position: relative;
  padding-top: 144px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 440px;
  height: 320px;
  gap: 20px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 30px 4px;
  justify-content: space-between;
}

.target {
  width: 80px;
  height: 80px;  
  overflow: hidden;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.faded {
  animation: faded 1s linear forwards;
}

@keyframes faded {
  0% { 
    transform: scaleX(-1) scaleY(100%) translateY(0); 
    opacity: 1;
  }
  5% { 
    transform: scaleX(-1) scaleY(110%) translateY(0%);
    opacity: 1;
  }
  25% { 
    transform: scaleX(-1) scaleY(60%) translateY(30%);
    opacity: 0.6;
  }
  60% { 
    transform: scaleX(-1) scaleY(60%) translateY(30%);
    opacity: 0.6;
  }
  75% { 
    transform: scaleX(-1) scaleY(60%) translateY(30%);
    opacity: 0;
  }
  100% { 
    transform: scaleX(-1) scaleY(100%) translateY(0); 
    opacity: 0;
  }
}